body,
html {
  width: 100%;
  height: 100%;
  background-color: #111414;
  color: #efefef;
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: inherit;
  border: none;
  box-shadow: none;
  border-radius: 0;
  overflow: visible;
  color: inherit;
  cursor: pointer;
}

.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100px;
  padding: 20px 40px;
  transition: top 0.3s ease-in-out;
  transform: translateY(-100%);
  animation: fadeIn 0.5s 2.8s forwards;
  opacity: 1;
  z-index: 999;
}

.header_logo, .header_menuButton {
  font-size: 1.1em;
  font-weight: 700;
}

@keyframes fadeIn {
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.header_logo {
  position: absolute;
  top: 50%;
  left: 40px;
  transform: translateY(-50%);
  transition: all 0.3s ease-in-out;
}

.header_logo.hide {
  top: -100%;
}

.header_menu_button {
  padding: 6px 10px;
  border: 2px solid #fbbf24;
  border-radius: 30px;
}

.header_nav {
  display: none;
  position: absolute;
  top: 74px;
  right: 0;
  text-align: center;
}

.header_nav .header_list {
  display: flex;
  flex-direction: column;
  text-align: right;
}

.header_nav .header_item {
  opacity: 0;
  position: relative;
  right: -100px;
  font-size: 1.2em;
  padding: 12px 40px;
  overflow: hidden;
}

.header_nav .header_item:hover {
  transform: rotate(26deg);
}

.header_nav .header_item:hover a {
  background-color: #c084fc;
}

.header_nav .header_item a {
  height: auto;
  padding: 6px 10px;
  border: 2px solid #fbbf24;
  background-color: #111414;
  border-radius: 30px;
}

.main section,
footer section {
  padding: 50px 10%;
}

.main {
  padding-top: 100px;
}

.main_section.intro {
  position: relative;
  height: 100vh;
}

.main_section.intro .intro_content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; /* 요소들을 수직, 수평 정렬하기 위하여 추가한 CSS 속성 */
}

.main_section.intro .intro_content .intro_main_title {
  position: absolute;
  font-size: 4.5rem;
  opacity: 0;
}

.main_section.intro .intro_content .intro_main_title.from-top {
  top: 19%;
  transform: translateY(-200%) scale(3);
  animation: mainTitleTop 1.5s forwards;
}

.main_section.intro .intro_content .intro_main_title.from-bottom {
  bottom: 30%;
  transform: translateY(200%) scale(3);
  animation: mainTitleBottom 1.5s forwards;
}

@keyframes mainTitleTop {
  65% {
    opacity: 1;
    transform: translateY(150px) scale(1);
  }
  75% {
    opacity: 1;
    transform: translateY(150px) scale(1);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
@keyframes mainTitleBottom {
  65% {
    opacity: 1;
    transform: translateY(-150px) scale(1);
  }
  75% {
    opacity: 1;
    transform: translateY(-150px) scale(1);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
.main_section.intro .intro_content .intro_img_box {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translate(-50%, -50%);
  height: 0;
  width: 260px;
  border-radius: 30px;
  animation: introImgBox 0.8s 1.2s forwards ease-in-out;
  background-size: cover;
  overflow: hidden;
}

.main_section.intro .intro_content .intro_img_box .intro_img {
  border-radius: 30px;
  width: 13.5416666667vw;
  height: 13.5416666667vw;
  max-width: 260px;
  max-height: 260px;
  min-width: 220px;
  min-height: 220px;
  background: url(../images/intro.png) center center no-repeat;
  background-size: cover;
}

@keyframes introImgBox {
  100% {
    height: 260px;
  }
}
.main_section.intro .intro_content .intro_sub_title {
  position: absolute;
  font-size: 1.2rem;
  text-align: center;
  opacity: 1;
}

.main_section.intro .intro_content .intro_sub_title.first {
  bottom: 22%;
  animation: fadeIn 1s 2s forwards;
  opacity: 0;
  transform: translateY(20px);
}

.main_section.intro .intro_content .intro_sub_title.second {
  opacity: 0;
  bottom: 19%;
  animation: fadeIn 1s 2.3s forwards;
  transform: translateY(20px);
}

.main_section.works .works_list .works_item {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 70px 0;
  border-bottom: 1px solid rgba(200, 200, 200, 0.2);
}

.main_section.works .works_list .works_item .works_info .works_title {
  font-size: 3.8em;
  line-height: 1.8;
}

.main_section.works .works_list .works_item .works_info .works_keywords {
  font-size: 1.4em;
}

.main_section.works .works_list .works_item .works_info .works_keywords .works_kinds {
  color: rgba(239, 239, 239, 0.6823529412);
  font-weight: 300;
  padding: 4px 6px;
}

.main_section.works .works_list .works_item .works_info .works_keywords .works_kinds:last-of-type {
  position: relative;
  padding-left: 20px;
  border: none;
}

.main_section.works .works_list .works_item .works_info .works_keywords .works_kinds:last-of-type::before {
  position: absolute;
  left: 0px;
  top: 50%;
  transform: translateY(-50%);
  content: "|";
}

.main_section.works .works_list .works_item .works_year .works_period {
  font-size: 1.6em;
  font-weight: 300;
  color: rgba(239, 239, 239, 0.6823529412);
}

.main_section.education {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 100px 10%;
}

.main_section.education .edu_info {
  max-width: 40%;
}

.main_section.education .edu_info .edu_title {
  font-size: 3.8em;
  line-height: 1.5;
  margin-bottom: 30px;
}

.main_section.education .edu_info .edu_desc {
  font-size: 1.6em;
  font-weight: 400;
}

.main_section.education .edu_list_wrap {
  min-width: 50%;
}

.main_section.education .edu_list_wrap .edu_list {
  text-align: left;
  font-size: 1.4em;
  font-weight: 300;
}

.main_section.education .edu_list_wrap .edu_list .edu_item {
  display: flex;
  align-items: center;
}

.main_section.education .edu_list_wrap .edu_list .edu_item .edu_period {
  min-width: 230px;
}

.main_section.education .edu_list_wrap .edu_list + .edu_list {
  margin-top: 50px;
}

.main_section.skills {
  overflow: hidden;
}

.main_section.skills .skills_list {
  display: flex;
  flex-wrap: nowrap;
  animation: scroll 1s linear infinite alternate-reverse;
  overflow: hidden;
  margin: 0 -10%;
  padding: 70px 0;
}

@keyframes scroll {
  0% {
    transform: translateX(10%);
  }
  100% {
    transform: translateX(-10%);
  }
}
.main_section.skills .skills_item {
  max-width: 200px;
  margin-right: 60px;
}

.main_section.skills .skills_img {
  width: 100%;
  vertical-align: top;
}

.main_section.contact .contact_content {
  position: relative;
}

.main_section.contact .contact_content .contact_image {
  position: absolute;
  top: 0;
  width: 6em;
  height: 6em;
}

.main_section.contact .contact_content .contact_image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 50%;
}

.main_section.contact .contact_content .contact_image::after {
  position: absolute;
  top: 0;
  right: 0;
  width: 1.2em;
  height: 1.2em;
  background-color: #c084fc;
  border: 0.125em solid #efefef;
  border-radius: 50%;
  z-index: 90;
  content: "";
}

.main_section.contact .contact_content .contact_title {
  text-indent: 2em;
  font-size: 3.8em;
  max-width: 60%;
  word-break: keep-all;
  margin-bottom: 20px;
}

.main_section .contact_mail {
  font-size: 2.39em;
  color: #c084fc;
  padding: 20px 0;
}

.section_title {
  position: relative;
  font-size: 38px;
  padding: 30px 0;
}

.section_title sup {
  font-size: 0.6em;
  color: #fbbf24;
}

.section_title:before {
  position: absolute;
  bottom: -1px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #fbbf24;
  content: "";
  transform: scaleX(0);
  transform-origin: left center;
  transition: transform 0.5s ease-out;
}

.section_title.animate_border:before {
  transform: scaleX(1);
}

.footer_copyright {
  position: relative;
  padding-top: 30px;
  color: rgba(255, 255, 255, 0.5);
}

.footer_copyright:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 1px;
  background: transparent;
  border-bottom: 1px solid #c084fc;
  content: "";
  transition: all 0.5s ease-out;
}

.footer_animate:after {
  width: 100%;
}

