	@mixin iconA($onlyPos:all) {
		@if $onlyPos == pos {
			background-position: 0px 0px;
		} @else {
			display:inline-block;
			width: 21px;
			height: 21px;
			background-position: 0px 0px;
			background-size: 21px 21px;
			background-image: url($spriteURL+'sprite.png'+$spriteCache);
			background-repeat: no-repeat;	
		}
	}

    .iconA {@include iconA();}
